'use client'

import { useEffect, useState } from 'react'
import Link from 'next/link'
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"

interface Subscription {
  id: string
  customerId: string
  startDate: string
  type: string
  price: number
  billingInterval: string
}

interface Customer {
  id: string
  name: string
}

export default function Abonnement() {
  const [subscriptions, setSubscriptions] = useState<Subscription[]>([])
  const [customers, setCustomers] = useState<Record<string, Customer>>({})

  useEffect(() => {
    // Last inn abonnementer og kunder
    const savedSubscriptions = JSON.parse(localStorage.getItem('subscriptions') || '[]')
    const savedCustomers = JSON.parse(localStorage.getItem('customers') || '[]')
    
    setSubscriptions(savedSubscriptions)
    
    // Lag et oppslagsverk for kunder
    const customerMap = savedCustomers.reduce((acc: Record<string, Customer>, customer: Customer) => {
      acc[customer.id] = customer
      return acc
    }, {})
    setCustomers(customerMap)
  }, [])

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Abonnementer</h1>
        <Link href="/abonnement/ny">
          <Button>Nytt abonnement</Button>
        </Link>
      </div>

      <div className="grid gap-4">
        {subscriptions.map((subscription) => (
          <Card key={subscription.id} className="p-4">
            <div className="flex justify-between items-start">
              <div>
                <h2 className="font-semibold">
                  {customers[subscription.customerId]?.name || 'Ukjent kunde'}
                </h2>
                <p className="text-sm text-gray-600">
                  Type: {subscription.type}
                </p>
                <p className="text-sm text-gray-600">
                  Pris: {subscription.price} NOK / {subscription.billingInterval}
                </p>
                <p className="text-sm text-gray-600">
                  Startdato: {new Date(subscription.startDate).toLocaleDateString('nb-NO')}
                </p>
              </div>
            </div>
          </Card>
        ))}

        {subscriptions.length === 0 && (
          <p className="text-center text-gray-500 py-8">
            Ingen abonnementer registrert enn√•
          </p>
        )}
      </div>
    </div>
  )
} 